import * as React from "react";
import * as ReactDOM from "react-dom";
import {Grid, Row, Col, Navbar, Nav, NavItem} from 'react-bootstrap';
import {
    BrowserRouter as Router,
    Route,
    Link
} from 'react-router-dom';
import {Blog} from './blog';

const navbar = (
    <Navbar inverse collapseOnSelect>
        <Navbar.Header>
            <Navbar.Brand>
                <a href="#">Huckleberry Ogre Homepage</a>
            </Navbar.Brand>
            <Navbar.Toggle />
        </Navbar.Header>
        <Navbar.Collapse>
            <Nav>
                <NavItem eventKey={1} href="#"><Link to="/">Blog</Link></NavItem>
                <NavItem eventKey={2} href="#"><Link to="/blog/post/new">New post</Link></NavItem>
            </Nav>
        </Navbar.Collapse>
    </Navbar>
);

ReactDOM.render(<div>
        <Grid>
            <Row className="show-grid">
                <Col xs={12}>
                    {navbar}
                    <Router>
                        <Route path="/" exact={true} component={Blog}/>
                        <Route path="/blog/post/new" component={Blog} />
                    </Router>
                </Col>
            </Row>
        </Grid>

    </div>
    ,
    document.getElementById("root")
);


